import React, { Component } from 'react';
import BookService from '../services/BookService'

class ListBookComponent extends Component {
    constructor(props) {
        super(props)

        this.state = {
            books: []
        }

        this.addBook = this.addBook.bind(this);
        this.deleteBook = this.deleteBook.bind(this);
        this.popBook = this.popBook.bind(this);

    }
 
    componentDidMount(){
        BookService.getBooks().then(res => {
            this.setState({ books: res.data});
        });
    }

    addBook(){
        this.props.history.push('/addnewbook');

    }

    deleteBook(id){
        BookService.deleteBook(id).then(res => {
            this.setState({books: this.state.books.filter(book => book.id !== id)});
        });
    }

    popBook(id){
        this.props.history.push('/acknbook/'+id);
    }

    render() {
        return (
            <div>
                <h1 className="text-left">-</h1>
                <div className= "button">
                    <button className="btn btn-dark" onClick={this.addBook}> Add Book</button>
                    <button className="btn btn-secondary" onClick={this.adminNGO} style={{marginLeft:"930px"}}> Admin/NGO </button>
                    <button className="btn btn-secondary" onClick={this.otherUser} style={{marginLeft:"20px"}}> Donor/Receiver </button>
                </div>
                <h2 className="text-center">-- All Books --</h2>
                <div className = "row">
                        <table className = "table table-bordered border-primary table-hover ">
                            <thead>
                                <tr>
                                    <th> Book ID </th>
                                    <th> Book Name </th>
                                    <th> Author </th>
                                    <th> Condition </th>
                                    <th> Book Owner </th>
                                    <th> Zip Code </th>
                                    <th> Donor Phone# </th>
                                    <th> Donor eMail </th>
                                    <th>  </th>
                                </tr>
                            </thead>

                            <tbody style={{color:'indigo', fontSize: '4mm'}}>
                                {
                                    this.state.books.map(
                                        book =>
                                        <tr key = {book.id}>
                                            <td> {book.id} </td>
                                            <td> {book.bookName} </td>
                                            <td> {book.authorName} </td>
                                            <td> {book.condition} </td>
                                            <td> {book.ownerName} </td>
                                            <td> {book.ownerZip} </td>
                                            <td> ************** </td>
                                            <td> ******** </td>
                                            <td>
                                                <button className="btn btn-secondary" onClick={ () => this.updateBook}> Update</button> 
                                                <button className="btn btn-dark" onClick={ () => this.deleteBook(book.id)} style={{marginLeft:"10px"}}> Delete</button> 
                                                <button className="btn btn-dark" onClick={ () => this.popBook(book.id)} style={{marginLeft:"10px"}}> Acknowledge</button>
                                            </td>
                                        </tr>
                                    )
                                }
                            </tbody>
                        </table>
                </div>
            </div>
        );
    }
}

export default ListBookComponent;