import axios from 'axios';

const BOOKFORALL_BASE_API_URL = "https://bookforall-humble-giraffe-jj.mybluemix.net/bookforallapi";

const FINDALLBOOK_API_URL = "https://bookforall-humble-giraffe-jj.mybluemix.net/bookforallapi/findAllBooks";
const ADDBOOK_API_URL = "https://bookforall-humble-giraffe-jj.mybluemix.net/bookforallapi/addBook";
const DLTBOOK_API_URL = "https://bookforall-humble-giraffe-jj.mybluemix.net/bookforallapi/delete";


class BookService {

    getBooks(){
        return axios.get(FINDALLBOOK_API_URL);
    }

    createBook(book){
        return axios.post(ADDBOOK_API_URL, book);

    }

    deleteBook(id){
        return axios.delete(DLTBOOK_API_URL + '/' + id);

    }

    getBook(id){
        return axios.get(FINDALLBOOK_API_URL + '/' + id);

    }

}

export default new BookService()