import React, { Component } from 'react';
import BookService from '../services/BookService';

class AcknBookComponent extends Component {
    constructor(props) {
        super(props)

        this.state = {
            id: this.props.match.params.id,
            bookName: '',
            authorName: '',
            condition: '',
            ownerName: '',
            ownerZip: '',
            phoneNum: '',
            email: ''
        }
        
    }


    componentDidMount(){
        BookService.getBook(this.state.id).then((res) => {
            let book = res.data;
            this.setState({id:book.id, 
                bookName:book.bookName, 
                authorName:book.authorName, 
                condition:book.condition, 
                ownerName:book.ownerName, 
                ownerZip:book.ownerZip, 
                phoneNum:book.phoneNum, 
                email:book.email
            });
        });

    }

    goback() {
        this.props.history.push('/allbooks');
    }


    render() {
        return (
            <div>
                <h1>line-1</h1>
                <h2></h2>

                <div className="container">
                    <div className="row">
                        <div className="card col-md-6 offset-md-3 offset-md-3">
                            <h3 className="text-center"> -- Book of Your Interest -- </h3>
                            <div className="card-body">
                                <form>
                                    <div className="form-screen3">
                                        <label> Book ID </label>
                                        <input className="form-control" style={{color:'black', fontWeight:'bold', fontSize: '5mm'}} value={this.state.id}/>
                                    </div>
                                    <div className="form-screen3">
                                        <label>Book Name </label>
                                        <input className="form-control" style={{color:'black', fontWeight:'bold', fontSize: '5mm'}} value={this.state.bookName}/>
                                    </div>
                                    <div className="form-screen3">
                                    <label>Author </label>
                                        <input className="form-control" style={{color:'black', fontWeight:'bold', fontSize: '5mm'}} value={this.state.authorName}/>
                                    </div>
                                    <div className="form-screen3">
                                        <label>Book Condition </label>
                                        <input className="form-control" style={{color:'black', fontWeight:'bold', fontSize: '5mm'}} value={this.state.condition}/>
                                    </div>
                                    <div className="form-screen3">
                                        <label>Book Owner </label>
                                        <input className="form-control" style={{color:'black', fontWeight:'bold', fontSize: '5mm'}} value={this.state.ownerName}/>
                                    </div>
                                    <div className="form-screen3">
                                    <label>Zip </label>
                                        <input className="form-control" style={{color:'black', fontWeight:'bold', fontSize: '5mm'}} value={this.state.ownerZip}/>
                                    </div>
                                    <div className="form-screen3">
                                        <label>Phone No. </label>
                                        <input className="form-control" style={{color:'black', fontWeight:'bold', fontSize: '5mm'}} value={this.state.phoneNum}/>
                                    </div>
                                    <div className="form-screen3">
                                        <label>eMail ID </label>
                                        <input className="form-control" style={{color:'black', fontWeight:'bold', fontSize: '5mm'}} value={this.state.email}/>
                                    </div>

                                    <div className="row">.</div>
                                    <button className="btn btn-dark" onClick={this.goback.bind(this)} style={{marginLeft:"10px"}}> Back </button>

                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default AcknBookComponent;