import React, { Component } from 'react';
import BookService from '../services/BookService';

class AddBookComponent extends Component {
    constructor(props) {
        super(props)

        this.state = {
            id: '',
            bookName: '',
            authorName: '',
            condition: '',
            ownerName: '',
            ownerZip: '',
            phoneNum: '',
            email: ''
        }

        this.changeIdHandler = this.changeIdHandler.bind(this);   
        
        this.saveBook = this.saveBook.bind(this);
    }

    saveBook = (e) => {
        e.preventDefault();

        let book = {id:this.state.id, 
            bookName:this.state.bookName, 
            authorName:this.state.authorName, 
            condition:this.state.condition, 
            ownerName:this.state.ownerName, 
            ownerZip:this.state.ownerZip, 
            phoneNum:this.state.phoneNum, 
            email:this.state.email
        };
        
        console.log('book => ' + JSON.stringify(book));

        BookService.createBook(book).then(res => {
            this.props.history.push('/allbooks');

        })

    }

    cancel() {
        this.props.history.push('/allbooks');
    }


    changeIdHandler= (event) => {
        this.setState({id: event.target.value});
    }
    changeBookNameHandler= (event) => {
        this.setState({bookName: event.target.value});
    }
    changeAuthorNameHandler= (event) => {
        this.setState({authorName: event.target.value});
    }
    changeConditionHandler= (event) => {
        this.setState({condition: event.target.value});
    }
    changeOwnerNameHandler= (event) => {
        this.setState({ownerName: event.target.value});
    }
    changeOwnerZipHandler= (event) => {
        this.setState({ownerZip: event.target.value});
    }
    changePhoneNumHandler= (event) => {
        this.setState({phoneNum: event.target.value});
    }
    changeEmailHandler= (event) => {
        this.setState({email: event.target.value});
    }


    render() {
        return (
            <div>
                <h1>line-1</h1>
                <h2></h2>

                <div className="container">
                    <div className="row">
                        <div className="card col-md-6 offset-md-3 offset-md-3">
                            <h3 className="text-center"> -- Share a Book -- </h3>
                            <div className="card-body">
                                <form>
                                    <div className="form-screen2">
                                        <label> Book ID </label>
                                        <input placeholder="Numeric Unique ID" name="id" className="form-control" value={this.state.id} onChange={this.changeIdHandler}/>
                                    </div>
                                    <div className="form-screen2">
                                        <label>Book Name </label>
                                        <input placeholder="Book Name" name="bookName" className="form-control" value={this.state.bookName} onChange={this.changeBookNameHandler}/>
                                    </div>
                                    <div className="form-screen2">
                                    <label>Author </label>
                                        <input placeholder="Author Name" name="authorName" className="form-control" value={this.state.authorName} onChange={this.changeAuthorNameHandler}/>
                                    </div>
                                    <div className="form-screen2">
                                        <label>Book Condition </label>
                                        <input placeholder="New or Used" name="condition" className="form-control" value={this.state.condition} onChange={this.changeConditionHandler}/>
                                    </div>
                                    <div className="form-screen2">
                                        <label>Book Owner </label>
                                        <input placeholder="Full Name" name="ownerName" className="form-control" value={this.state.ownerName} onChange={this.changeOwnerNameHandler}/>
                                    </div>
                                    <div className="form-screen2">
                                    <label>Zip </label>
                                        <input placeholder="Pin Code" name="ownerZip" className="form-control" value={this.state.ownerZip} onChange={this.changeOwnerZipHandler}/>
                                    </div>
                                    <div className="form-screen2">
                                        <label>Phone No. </label>
                                        <input placeholder="10 digit phone number" name="phoneNum" className="form-control" value={this.state.phoneNum} onChange={this.changePhoneNumHandler}/>
                                    </div>
                                    <div className="form-screen2">
                                        <label>eMail ID </label>
                                        <input placeholder="eMail (optional)" name="email" className="form-control" value={this.state.email} onChange={this.changeEmailHandler}/>
                                    </div>

                                    <div className="row">.</div>
                                    <button className="btn btn-dark" onClick={this.saveBook}> Submit </button>
                                    <button className="btn btn-dark" onClick={this.cancel.bind(this)} style={{marginLeft:"10px"}}> Cancel </button>

                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default AddBookComponent;