import logo from './logo.svg';
import './App.css';
import ListBookComponent from './components/ListBookComponent';
import AddBookComponent from './components/AddBookComponent';
import AcknBookComponent from './components/AcknBookComponent';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import HeaderComponent from './components/HeaderComponent';
import FooterComponent from './components/FooterComponent';

function App() {
  return (
    <div>
      <Router>
        <HeaderComponent />
          <div className="container">
            <Switch>
              <Route path = "/" exact component = {ListBookComponent}></Route>
              <Route path = "/allbooks" component = {ListBookComponent}></Route>
              <Route path = "/addnewbook" component = {AddBookComponent}></Route>
              <Route path = "/acknbook/:id" component = {AcknBookComponent}></Route>
              <ListBookComponent />
            </Switch>
          </div>
        <FooterComponent />
      </Router>
    </div>
  );
}

export default App;
